.products {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  border: 1px solid #999;
  margin: 20px;
}

.product {
  border: 1px solid #999;
  flex: 0.4;
  margin: 10px;
  padding: 10px;
}

.title {
  text-align: center;
}

.body {
  display: flex;
}

.body img {
  object-fit: "contain";
  width: 350px;
  height: 350px;
}

.action_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  margin: 0px 10px;
}

.buy {
  background-color: rgb(48, 0, 124);
  color: #fff;
}
